export default [
  {
    selector: 'node',
    style: {
      'content': 'data(id)',
      'text-opacity': 1,
      'font-size': 4,
      'height': 10,
      'width': 10,
      'background-color': '#AAA',
      'color': '#AAA',
      'font-weight': 'bold',
      'text-halign': 'right',
      'text-valign': 'center',
      'text-margin-x': 2,
      'shape': 'roundrectangle'
    }
  },
  {
    selector: ':parent',
    style: {
      'background-opacity': 0.1,
      'text-valign': 'top',
      'text-halign': 'center',
      'text-margin-y': -3
    }
  },
  {
    selector: 'edge',
    style: {
      'curve-style': 'bezier',
      'width': 0.5,
      'target-arrow-shape': 'triangle',
      'line-color': '#AAA',
      'target-arrow-color': '#AAA'
    }
  },
  {
    selector: ':selected',
    style: {
      'border-width': 0.5,
      'background-color': '#448aff',
      'border-color': '#1e4fa1'
    }
  }
]



