export default class {
  constructor (cy) {
    const api = cy.expandCollapse({
      layoutBy: {
        name: 'dagre',
        animate: true,
        randomize: false,
        fit: true
      },
      expandCollapseCueSize: 4,
      expandCollapseCueLineSize: 0.5,
      fisheye: false,
      undoable: false
    })

    api.collapseAll()
    api.collapseRecursively(cy.$(':selected'))
  }
}



